.header {
    display: flex;
    justify-content: center;
    align-items: center;
}

.name {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    font-family: "Lucida Console", "Courier New", monospace;
    font-size: 20px;
}

.intro {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    font-family: "Lucida Console", "Courier New", monospace;
    font-size: 15px;
}


.corgi {
    display: flex;
    justify-content: center;
    align-items: center;

}

.corgi_pic {
    border-radius: 50%;
}

.tim_pic {
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 500px;
    font-family: "Lucida Console", "Courier New", monospace;
    font-size: 50px;
}

.name_about {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    font-family: "Lucida Console", "Courier New", monospace;
    font-size: 30px;
}

.profile {
    font-family: "Lucida Console", "Courier New", monospace;
    font-size: 20px;
}

body {
    background-color: aliceblue;
    margin: 0px;
    min-height: 100%;
    width: 100%;
}